import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import { LandingPage } from './pages/Landing';
import { ThemeProvider } from 'styled-components';
import { theme } from './theme';

function App() {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Switch>
          <Route path='/'>
            <LandingPage />
          </Route>
        </Switch>
      </Router>
    </ThemeProvider>
  );
}

export default App;
